<template>
    <div class="page-wrap">
        <div class="title-container">
            <h1 class="ranking-title">&lt; 신마법의 대륙 랭킹 v1.2 &gt;</h1>
            <span class="current-date"> 조회시간 : {{ currentDate }}</span>
        </div>
        <table v-if="rankData.length">
            <thead>
                <tr>
                    <th v-for="header in tableHeaders" :key="header">
                        {{ header }}
                    </th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in rankData" :key="item.rank">
                    <td>{{ item.rank }}</td>
                    <td>
                        <img
                            v-if="item.rank === 1"
                            src="/icons/gold-icon.png"
                            alt="Gold Medal"
                            class="rank-icon"
                        />
                        <img
                            v-if="item.rank === 2"
                            src="/icons/silver-icon.png"
                            alt="Silver Medal"
                            class="rank-icon"
                        />
                        <img
                            v-if="item.rank === 3"
                            src="/icons/bronze-icon.png"
                            alt="Bronze Medal"
                            class="rank-icon"
                        />
                        {{ item.name }}
                    </td>
                    <td class="class-container">
                        <img
                            :src="getClassImageUrl(item.class)"
                            :alt="item.class"
                            class="class-icon"
                        />
                        <span class="class-name">{{ item.class }}</span>
                    </td>
                    <td>{{ formatNumber(calculateRebirth(item.remotal)) }}</td>
                    <td>{{ formatNumber(item.remotal) }}</td>
                    <td>{{ formatNumber(item.level) }}</td>
                    <td>{{ item.tribe }}</td>
                </tr>
            </tbody>
        </table>

        <div class="pagination">
            <button
                v-for="page in totalPages"
                :key="page"
                @click="changePage(page)"
                :class="{ active: currentPage === page }"
            >
                {{ page }}
            </button>
        </div>

        <div class="icon-desc-wrap">
            <a
                href="https://www.flaticon.com/kr/free-icons/"
                title="메달 아이콘"
                >메달 아이콘 제작자 : Freepik - Flaticon</a
            >
        </div>
    </div>
</template>

<script setup>
import { ref, onMounted, watch } from "vue";
import { getRanking } from "@/api";
import dayjs from "dayjs";

const currentDate = ref("");
const rankData = ref([]);
const tableHeaders = [
    "순위",
    "캐릭터",
    "직업",
    "환생",
    "리모탈",
    "레벨",
    "종족",
];
const currentPage = ref(1);
const totalPages = 10;

function formatNumber(num) {
    return num.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

function calculateRebirth(remotal) {
    if (remotal <= 250) {
        return 0;
    }
    return Math.floor((remotal - 1) / 250);
}

function parseRankingData(htmlString) {
    const parser = new DOMParser();
    const doc = parser.parseFromString(htmlString, "text/html");
    const rows = doc.querySelectorAll("tr.grid_class_rows");

    return Array.from(rows).map((row) => {
        const cells = row.querySelectorAll("td");
        return {
            rank: parseInt(cells[0].textContent, 10),
            name: cells[1].textContent,
            remotal: parseInt(cells[2].textContent, 10),
            level: parseInt(cells[3].textContent, 10),
            tribe: cells[4].textContent,
            class: cells[5].textContent,
        };
    });
}

const changePage = (newPage) => {
    if (newPage >= 1 && newPage <= totalPages) {
        currentPage.value = newPage;
    }
};

watch(currentPage, (newPage) => {
    fetchRanking(newPage);
});

onMounted(() => {
    fetchRanking(currentPage.value);
});

const fetchRanking = async (page) => {
    try {
        const res = await getRanking(page);
        rankData.value = parseRankingData(res);
        currentDate.value = dayjs().format("YYYY-MM-DD HH:mm:ss");
    } catch (err) {
        console.error("Failed to fetch ranking data:", err);
    }
};

function getClassImageUrl(className) {
    const filename = convertClassToFilename(className);
    return `/images/${filename}`;
}

const convertClassToFilename = (className) => {
    switch (className) {
        case "도둑":
            return "thief.png";
        case "전사":
            return "warrior.png";
        case "소환":
            return "summoner.png";
        case "마법":
            return "mage.png";
        case "성직":
            return "cleric.png";
        case "음유":
            return "bard.png";
        default:
            console.warn(`Unknown class name: ${className}`);
            return "unknown.png";
    }
};
</script>

<style scoped>
.page-wrap {
    padding: 8px;
}
.title-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 8px;
    margin-bottom: 12px;
}

.ranking-title {
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    margin-right: 15px;
}

.current-date {
    font-size: 14px;
    color: #666;
}

table {
    width: 100%;
    border-collapse: collapse;
}
tr {
    height: 36px;
}
th,
td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: center;
    vertical-align: middle;
}
th {
    background-color: #979797;
    font-weight: bold;
}
tbody tr:nth-child(even) {
    background-color: #ebebeb;
}
th:nth-child(1),
td:nth-child(1) {
    width: 10%;
}
th:nth-child(2),
td:nth-child(2) {
    width: 25%;
}
th:nth-child(3),
td:nth-child(3) {
    width: 20%;
}
th:nth-child(4),
td:nth-child(4) {
    width: 10%;
}
th:nth-child(5),
td:nth-child(5) {
    width: 15%;
}
th:nth-child(6),
td:nth-child(6) {
    width: 10%;
}
th:nth-child(7),
td:nth-child(7) {
    width: 10%;
}

.pagination {
    margin: 20px 0;
    text-align: center;
}
.pagination button {
    margin: 0 5px;
    padding: 5px 0;
    cursor: pointer;
    width: 30px; /* 고정된 너비 */
    height: 30px; /* 고정된 높이 */
    font-size: 12px; /* 폰트 크기 줄임 */
    line-height: 1; /* 줄 높이를 조정하여 텍스트 세로 중앙 정렬 */
    text-align: center; /* 텍스트 가로 중앙 정렬 */
}
.pagination button.active {
    color: red;
    font-weight: 900;
}
.class-icon {
    width: 16px;
    height: 16px;
    vertical-align: middle;
}

.rank-icon {
    width: 16px;
    height: 16px;
    vertical-align: middle;
}
.class-name {
    display: inline;
    margin-left: 4px;
}

.icon-desc-wrap {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}

@media (max-width: 520px) {
    th:nth-child(1),
    td:nth-child(1) {
        font-size: 12px;
    }
    th:nth-child(2),
    td:nth-child(2) {
        font-size: 12px;
    }
    th:nth-child(3),
    td:nth-child(3) {
        font-size: 12px;
    }
    th:nth-child(4),
    td:nth-child(4) {
        font-size: 12px;
    }
    th:nth-child(5),
    td:nth-child(5) {
        font-size: 12px;
    }
    th:nth-child(6),
    td:nth-child(6) {
        font-size: 12px;
    }
    th:nth-child(7),
    td:nth-child(7) {
        font-size: 12px;
    }

    .class-name {
        display: none;
    }
}
</style>
