import axiosInstance from "./axiosInstance";

// GET 요청 함수 (API 호출 결과를 호출한 쪽에서 처리)
export const getRanking = async (page) => {
    try {
        const response = await axiosInstance.get(
            `/ranking/ranking_content.php?server=&ranktype=&page=${page}`
        );
        return response.data; // 성공 시 데이터를 반환
    } catch (error) {
        console.error("Error fetching ranking:", error);
        throw error; // 에러 발생 시 throw로 에러를 호출한 쪽으로 전달
    }
};
